import React from 'react'
import styled from 'styled-components'
import ourleft from '../images/ourleft.jpg'
import icon from '../images/icon.png'

const Our = () => {
    return (
        <Section>
            <div className="container">
                <div className="row d-flex justify-content-between align-items-center">
                    <div className="col-lg-7 col-md-6 col-sm-12">
                        <img src={ourleft} className="img-fluid" alt="" />
                    </div>
                    <div className="col-lg-5 col-md-6 col-12">
                        <div id="two" className="d-flex align-items-center mb-lg-5">
                            <img src={icon} alt="" />
                            <h5>Our team</h5>
                        </div>
                        <div className="lyrics">
                            <p>Our consultants, all of whom come from different EU and CIS countries, possess high-level expertise and extensive experience. Their participation in our projects allows us to effectively build up the work process, avoid pitfalls and pass the experience on to the customer’s employees.</p>
                            <p>Our experts’ knowledge of country-specific and cultural characteristics, as well as local legal practices, allows them to form functional solutions and effectively interact with all stakeholders. We actively cooperate with local partners throughout the world.</p>
                        </div>
                    </div>
                </div>
            </div>
        </Section>
    )
}
const Section = styled.div`
    background: #F2F4F9;
    padding: 5em 0;
    h5{
        margin: 0 0 0 0.5em;
        font-family: Inter;
        font-style: normal;
        font-weight: bold;
        font-size: 42px;
        line-height: 51px;
        display: flex;
        align-items: center;
        color: #171717;
    }
    .lyrics{
        font-family: Inter;
        font-style: normal;
        font-weight: 300;
        font-size: 16px;
        line-height: 135%;
        color: #171717;
    }
    @media screen and (max-width: 500px){
        padding: 2em 0;
        h5{
        margin: 0 0 0 0.5em;
        font-family: Inter;
        font-style: normal;
        font-weight: bold;
        font-size: 32px;
        line-height: 39px;
        display: flex;
        align-items: center;
        color: #171717;
    }
    .lyrics{
        font-family: Inter;
        font-style: normal;
        font-weight: 300;
        font-size: 15px;
        line-height: 135%;
        color: #171717;
    }
        .col-lg-7{
            order: 2;
        }
        #two{
            margin: 2em 0;
        }
    }
`
export default Our
