import React from 'react'
import icon from '../images/icon.png'
import aboutright from '../images/aboutright.jpg'
import styled from 'styled-components'

const About = () => {
    return (
        <Section>
            <div className="container">
                <div className="d-flex align-items-center my-5">
                    <img src={icon} alt="" />
                    <h5>About Us</h5>
                </div>
                <div className="row d-flex justify-content-between align-items-center">
                    <div className="col-lg-6 col-md-12 col-sm-12">
                        <div className="text">
                            <div className="d-flex justify-content-around align-items-center py-1">
                                <h1>1</h1>
                                <h1 className="sistem">System integration services</h1>
                                <div className="minus"></div>
                            </div>
                            <div className="lyrics">
                                <p>Enterprise integration services that help brands, their customers and employees become better connected to the digital experiences of today, and those coming tomorrow,all while delivering improved business results.</p>
                                <p>Strategic business outcomes, technology optimization and ongoing organization enablement are key drivers of Leader Group’s system integration services, serving to achieve customer success.</p>
                                    <p>Key platform decisions, countless configuration options and far-reaching technology implications are challenges for even the most sophisticated companies. Our full-stack expertise transforms your goals into measurable results, so you choose well, navigate the landscape and avoid pitfalls.</p>
                            </div>
                            </div>
                            <div id="two" className="d-flex justify-content-between align-items-center">
                                <h1>2</h1>
                                <h1>Consulting services</h1>
                                <div className="minus"><div className="plus"></div></div>
                            </div>
                            <div id="three" className="d-flex justify-content-between align-items-center">
                                <h1>3</h1>
                                <h1>Project management</h1>
                                <div className="minus"><div className="plus"></div></div>
                            </div>

                    </div>
                    <div className="col-lg-6 col-md-12 col-sm-12">
                        <div className="bg ms-lg-5">
                            <img src={aboutright} className="img-fluid" alt="" />
                        </div>
                    </div>
                </div>
            </div>  
        </Section>
    )  
}
const Section = styled.div`
    padding: 7em 0;
    /* .bg{
        background: url(${aboutright}) no-repeat center center/cover;
        height: 59vh;
    } */
    h5{
        margin: 0 0 0 0.5em;       
        font-family: Inter;
        font-style: normal;
        font-weight: bold;
        font-size: 42px;
        line-height: 51px;
        display: flex;
        align-items: center;
        color: #171717;
    }
    .minus{
        width: 30px;
        height: 2px;
        background: #FED652;
        display: flex;
    }
    .plus{
        width: 30px;
        height: 2px;
        background: #FED652;
        transform: rotate(90deg);
    }
    #two{
        border-bottom: 2px solid #E4E4E4;
        padding: 1em 0;
    }
    #three{
        border-bottom: 2px solid #E4E4E4;
        padding: 1em 0;
    }

    .text{
        border-top: 2px solid #E4E4E4;
        border-bottom: 2px solid #E4E4E4;
        padding: 1em 0;
    }
    .lyrics{
        font-family: Inter;
        font-weight: 300;
        font-style: normal;
        line-height: 135%;
        font-size: 16px;
        color: #7D7D7D;
    }


    @media screen and (max-width: 500px){
        padding: 1em 0;
        h5{
            font-family: Inter;
            font-style: normal;
            font-weight: bold;
            font-size: 32px;
            line-height: 39px;
            display: flex;
            align-items: center !important;
            color: #171717;
        }
        h1{            
            /* font-family: Inter;
            font-style: normal; */
            font-weight: 500;
            font-size: 24px;
            line-height: 110%;
            color: #171717;
        }
        .sistem{
            margin: 0 0 0 15px;
        }
        .lyrics{
            font-family: Inter;
            font-style: normal;
            font-weight: 300;
            font-size: 15px;
            line-height: 135%;
            color: #7D7D7D;
        }
        .bg{
            margin-top: 1.5em;
        }
    }
`
export default About
