import React from 'react'
import styled from 'styled-components'
import footer from '../images/footer.jpg'
import logo from '../images/logofooter.jpg'

const Footer = () => {
    return (
        <Section>
            <div className="container">
                <div className="grid-container">
                    <div className="grid-item">
                        <img src={logo} className="img-fluid" alt="" />
                    </div>
                    <div className="grid-items">
                        <h4>Location:</h4>
                        <p>29 Bulgaria str., Ravda, Burgas, Bulgaria</p>
                    </div>
                    <div className="grid-items">
                        <h4>E-mail:</h4>
                        <p>Bulgaria@gmail.com</p>
                    </div>
                    <div className="grid-item">
                        <h4 className="orde">Contact whith us:</h4>
                        <div className="grid-item form">
                            <input type="text" placeholder="E-mail:" class="form-control w-50"/>
                            <input type="button" value="Send" class="btn ms-lg-2"/>
                        </div>
                    </div>
                </div>
            </div>
        </Section>
    )
}
const Section = styled.div`
    background: url(${footer}) no-repeat center center/cover;
    padding: 3rem 0;
    display: flex;
    align-items: center;
    .form{
        display: flex;
    }
    .grid-container {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr 1fr;
        }
    .btn{
        width: 8rem;
        background: #343D66;
        border-bottom: 2px solid yellow;
        color: white;
    }
    @media screen and (max-width: 500px){
        padding: 2rem 0;
        .img-fluid{
            text-align: center !important;
            margin: 2em 0;
        }
        .grid-container {
            display: grid;
            grid-template-columns: 1fr;
        }
        .form{
            display: block;
            input{
                width: 100% !important;
            }
        }
        .btn{
        /* width: 100%; */
        margin-top: 0.5em;
        background: #343D66;
        border-bottom: 2px solid yellow;
        color: white;
    }
    .grid-items{
        display: flex;
        justify-content: space-between;
        align-items: center !important;
        line-height: 2.5em;
        p{
            font-size: 14px;
        }
    }
    }
`
export default Footer
